<template>
    <div>
        {{nowDate}}{{nowWeek}} {{nowTime}}
    </div>
</template>

<script>
  export default {
    name: "istime",
    data(){
      return {
        nowDate: "",    // 
        nowTime: "",    // 
        nowWeek: ""     // 
      }
    },
    destroyed() {
      if (this.timer) {  // vue
        clearInterval(this.timer);
      }
    },
    methods:{
      dealWithTime(data) { // 
        let formatDateTime;
        let Y = data.getFullYear();
        let M = data.getMonth() + 1;
        let D = data.getDate();
        let H = data.getHours();
        let Min = data.getMinutes();
        let S = data.getSeconds();
        let W = data.getDay();
        H = H < 10 ? "0" + H : H;
        Min = Min < 10 ? "0" + Min : Min;
        S = S < 10 ? "0" + S : S;
        switch (W) {
          case 0:
            W = "";
            break;
          case 1:
            W = "";
            break;
          case 2:
            W = "";
            break;
          case 3:
            W = "";
            break;
          case 4:
            W = "";
            break;
          case 5:
            W = "";
            break;
          case 6:
            W = "";
            break;
          default:
            break;
        }
        this.nowDate = Y + "" + M + "" + D + " ";
        this.nowWeek = "" + W ;
        this.nowTime = H + ":" + Min + ":" + S;
      },
    },
    mounted() {
      this.dealWithTime(new Date())
      this.timer = setInterval(()=> {
        this.dealWithTime(new Date()) // date
      }, 500)
    }

  }
</script>

